import React from 'react';
import './scss/ConfirmModal.scss'
import { useDispatch,useSelector } from 'react-redux';
import { confirmModal } from '../reducer/confirmModal';


export default function ConfirmModalComponent(){

    const dispatch = useDispatch();
    const selector = useSelector((state)=>state);

    const onClickClose=(e)=>{
        e.preventDefault();
        dispatch(confirmModal(false));

    }
    return (
        <div id='confirmModal'>
            <div className="container">
                <div className="content">
                    <div className="header">
                        <div className="title">
                            <h2></h2>
                        </div>
                        <div className="text">
                            <p>  ,<strong> 1 /</strong></p>
                            <span>  ,   ?</span>
                        </div>
                    </div>
                    <div className="body">
                        <div className="img-box">
                            <img src="./images/confirm/img1.gif" alt="" />
                            <img src="./images/confirm/img2.png" alt="" />
                        </div>
                    </div>
                    <div className="footer">
                        <div className="text-box">
                            <span>5,000   <em>   </em><strong> !!</strong></span>
                            <div className="ment">
                                <h2>24, , , NHN </h2>
                                <h3>     .</h3>
                            </div>
                        </div>
                        <div className="button-box">
                            <button onClick={onClickClose} className='close-btn'>
                                <i></i>
                            </button>
                            <button className='ok-btn'><i></i></button>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    );
};
